<template>
  <div class="container" id="log">
    <div class="form row">
      <form
        method="post"
        action="../../../../static/loginpage/login.php"
        class="form-horizontal col-sm-offset-3 col-md-offset-3"
        id="login_form"
      >
        <h3 class="form-title">用户登入</h3>
        <div class="col-sm-9 col-md-9">
          <div class="form-group">
            <i class="fa fa-user fa-lg"></i>
            <input
              class="form-control required"
              type="text"
              placeholder="Username"
              name="username"
              autofocus="autofocus"
              maxlength="20"
            >
          </div>
          <div class="form-group">
            <i class="fa fa-lock fa-lg"></i>
            <input
              class="form-control required"
              type="password"
              placeholder="Password"
              name="password"
              maxlength="8"
            >
          </div>
          <div class="form-group">
            <select name="select" class="form-control required">
              <option value selected="selected">请选择身份类型</option>
              <option value="0">在校生</option>
              <option value="1">教职工</option>
              <option value="2">企业用户</option>
            </select>
          </div>
          <div class="form-group">
            <label class="checkbox">
              <input type="checkbox" name="remember" value="1"> Remember me
            </label>
            <hr>
          </div>
          <div class="form-group">
            <input type="submit" class="btn btn-success pull-right" value="Login ">
            <input
              type="button"
              id="register_btn"
              @click="register()"
              class="btn btn-primary pull-right"
              value="注册"
            >
          </div>
        </div>
      </form>
    </div>

    <div class="form row">
      <form
        class="form-horizontal col-sm-offset-3 col-md-offset-3"
        id="register_form"
        style="display:none"
      >
        <h3 class="form-title">账户注册</h3>
        <div class="col-sm-9 col-md-9">
          <div class="form-group">
            <i class="fa fa-user fa-lg"></i>
            <input
              class="form-control required"
              type="text"
              placeholder="Username"
              name="username"
              autofocus="autofocus"
            >
          </div>
          <div class="form-group">
            <i class="fa fa-lock fa-lg"></i>
            <input
              class="form-control required"
              type="password"
              placeholder="Password"
              id="register_password"
              name="password"
            >
          </div>
          <div class="form-group">
            <i class="fa fa-check fa-lg"></i>
            <input
              class="form-control required"
              type="password"
              placeholder="Re-type Your Password"
              name="rpassword"
            >
          </div>
          <div class="form-group">
            <i class="fa fa-envelope fa-lg"></i>
            <input class="form-control eamil" type="text" placeholder="Email" name="email">
          </div>
          <div class="form-group">
            <input type="submit" class="btn btn-success pull-right" value="Sign Up ">
            <input
              type="submit"
              class="btn btn-info pull-left"
              id="back_btn"
              value="Back"
              @click="back()"
            >
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import h from "../../../../static/loginpage/jquery.validate.min";
export default {
  data() {
    return {
      tag: false // 设置为成功登入的标志
    };
  },
  methods: {
    register() {
      document.getElementById("register_form").style.display = "block";
      document.getElementById("login_form").style.display = "none";
    },
    back() {
      document.getElementById("register_form").style.display = "none";
      document.getElementById("login_form").style.display = "block";
    }
  },
  mounted() {
    $("#login_form").validate({
      rules: {
        username: "required",
        password: {
          required: true,
          minlength: 5
        }
      },
      messages: {
        username: "请输入姓名",
        password: {
          required: "请输入密码",
          minlength: jQuery.format("密码不能小于{0}个字符")
        }
      }
    });
    $("#register_form").validate({
      rules: {
        username: "required",
        password: {
          required: true,
          minlength: 5
        },
        rpassword: {
          equalTo: "#register_password"
        },
        email: {
          required: true,
          email: true
        }
      },
      messages: {
        username: "请输入姓名",
        password: {
          required: "请输入密码",
          minlength: jQuery.format("密码不能小于{0}个字符")
        },
        rpassword: {
          equalTo: "两次密码不一样"
        },
        email: {
          required: "请输入邮箱",
          email: "请输入有效邮箱"
        }
      }
    });
  }
};
</script>

<style scoped>
#log {
  background-size: cover;
  font-size: 1em;
}
.form {
  background: rgba(255, 255, 255, 0.2);
  width: 400px;
  margin: 1em auto;
  background: url(../../../../static/images/loginpage/login.jpg) no-repeat;
}
#login_form {
  display: block;
}
#register_form {
  display: none;
}
.fa {
  display: inline-block;
  top: 27px;
  left: 6px;
  position: relative;
  color: #ccc;
}
input[type="text"],
input[type="password"] {
  padding-left: 26px;
}
.checkbox {
  padding-left: 21px;
}
</style>


