<template>
  <div id="oneheader">
    <div class="container">
      <div class="row clearfix">
        <div class="col-md-12 column">
          <div class="col-xs-12 col-sm-6 column">
            <img
              src="../../static/images/one_header/logo1.jpg"
              alt="logo"
              title="锐耳人力资源"
              class="img-responsive img-rounded center-block"
            >
          </div>
          <div class="col-xs-12 col-sm-6 column">
            <h1>锐耳人力资源</h1>
            <article style="text-indent:2em;">
              {{aboutMsg}}
            </article>
          </div>
 
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'oneheader',
  data() {
    return {
      aboutMsg: '大大耳朵，笨手又笨脚，跑步像陀螺，一动小脑筋总是出错，想要做好事，但总闯祸。爸爸说我是个，是个机灵鬼，妈妈摇头叫我，叫我淘气包！'
    };
  }
};
</script>

<style scoped>
#one_header {
  margin-bottom: 1em;
}
</style>



